.parallax-landing {
  background-image: url("../../../images/parallax.png");
  background-color: #F4F5F9; }

.parallax {
  background-image: url("../../../images/parallax.png");
  background-color: #405063; }

.header-landing {
  overflow: hidden;
  min-height: 5.3em;
  background-color: #F4F5F9; }

.header {
  background-color: #405063;
  min-height: 5.1em;
  background-image: url("../../../images/parallax.png"); }

.navbar-fixed {
  padding: 1.2em 0 0 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10000;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease; }

.shrink {
  padding: 1rem 0 !important; }

@media (min-width: 600px) {
  .navbar-fixed {
    padding: 2.7em 0 0 0; }
  .header {
    min-height: 8em; } }

@media (min-width: 1024px) {
  .header {
    padding-top: 0.5em;
    min-height: 23.5em; } }



